
import React from 'react';

export default class Capture extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      form: {}
    }
  }

  onFormSubmit(e) {
    e.preventDefault()
    this.props.onFormSubmit(this.state.form)
  }

  handleChange(name, event) {
    let form = this.state.form
    form[name] = event.target.value
    this.setState({
      form: form
    });
  }

  render () {
    return (
      <div className={this.props.classes.component_wrapper}>
        <div className="leadform-note-title" dangerouslySetInnerHTML={{__html: this.props.info.title}}></div>
        <p dangerouslySetInnerHTML={{__html: this.props.info.content}}></p>
        <form onSubmit={this.onFormSubmit.bind(this)}>
          {this.props.fields.map((field, index) =>
            <div className={this.props.classes.formgroup_wrapper} key={index}>
              <label>{field.title}</label>
              <input type={field.type} placeholder={field.placeholder} required className={field.className} onChange={this.handleChange.bind(this, field.name)} />
            </div>
          )}
          <div className="leadform-note-form-group">
            <input type="submit" value={this.props.form.buttonText} />
          </div>
        </form>
      </div>
    )
  }
}


