// React
import React from 'react';
import { render } from 'react-dom';

// Superagent
import Request from 'superagent';

// Components
import Thanks from './Components/Thanks/Thanks.jsx'
import Capture from './Components/Capture/Capture.jsx'
import ErrorCmp from './Components/ErrorCmp/ErrorCmp.jsx'

// Get widget info
var wrapper = document.getElementById('leadform-wrapper');
var widget_id = wrapper.getAttribute('data-widget-id');
var widget_position = wrapper.getAttribute('data-widget-position');

// Styles
require('./styles.scss');

// App
class App extends React.Component {

  constructor(props) {
    super(props);
    
    this.state = {
      api_url: 'https://jsonplaceholder.typicode.com/posts',
      open: false,
      captured: true,
      classes: {
        formgroup_wrapper: '',
        component_wrapper: '',
        input_field: '',
      },
      error: false,
      error_info: {
        title: 'Error',
        content: '<p>Something went wrong. Please try again.</p>'
      },
      fields: [
        {name:'email', type: 'email', title: 'Email', placeholder:'Enter Email Addresssss', isRequired:true}
      ],
      info: {
        bartitle: 'Sign up to our newsletter',
        title: 'Sign up to our newsletter',
        content: '' 
      },
      thanks: {
        title: 'Thanks for signing up',
        content: 'Thanks for signing up to our newsletter.'
      },
      form: {
        buttonText: 'Sign up'
      }
    }
  }

  componentDidMount() {
    this.setDefaults()
    this.checkIfShouldPopup()
  }

  setDefaults() {

    // Api url
    let api_url = (typeof leadform.api_url !== 'undefined') ? leadform.api_url : this.state.api_url

    let classes = (typeof leadform.classes !== 'undefined') ? leadform.classes : this.state.classes

    let fields = (typeof leadform.fields !== 'undefined') ? leadform.fields : this.state.fields

    // Info
    let info = Object.assign(this.state.info, leadform.info)
    
    // Thanks
    let thanks = Object.assign(this.state.thanks, leadform.thanks)
    
    // Form
    let form = Object.assign(this.state.form, leadform.form)

    // Error
    let error_info = Object.assign(this.state.error_info, leadform.error_info)

    // Set new defaults
    this.setState({
      api_url: api_url,
      classes: classes,
      fields: fields, 
      info: info,
      thanks: thanks,
      form: form,
      error_info: error_info
    })
  }

  checkIfShouldPopup() {
    let isCaptured = localStorage.getItem('leadform-captured-' + widget_id)
    if(isCaptured == null) {
      this.setState({
        captured: false
      })
    }
  }

  onFormSubmit(form) {
    Request
    .post(this.state.api_url)
    .send({ form: form })
    .set('Accept', 'application/json')
    .then((res, err) => {
        if(err) {
          this.setState({
            error: true
          })
        } else {
          this.setState({
            captured: true
          })
          localStorage.setItem('leadform-captured-'+widget_id, 'yes')
        }
    });
  }

  render() {
    // Content
    let innerContent

    if (this.state.error) {
      innerContent = <ErrorCmp error={this.state.error_info} />
    } else if (this.state.captured) {
      innerContent = <Thanks thanks={this.state.thanks} />
    } else {
      innerContent = <Capture onFormSubmit={this.onFormSubmit.bind(this)} info={this.state.info} form={this.state.form} classes={this.state.classes} fields={this.state.fields} />
    }

    return (
      <div id="leadform-wrapper">
        {innerContent}
      </div>
    )
  }
}

render(
  <App widgetId={widget_id} />, 
  document.querySelector('#leadform-wrapper')
);


